@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
.nav{display:flex;justify-content:space-between;align-items:center;position:-webkit-sticky;position:sticky;padding:0.6rem 1rem;font-size:x-large;margin:0.5rem 0;color:white}.nav *{color:white}.nav::after{content:'';position:absolute;top:0;left:0;top:0;left:0;right:0;bottom:0;z-index:1}.nav>*{z-index:2}.nav ul{list-style:none}.nav ul li{padding:0 0.7rem;display:inline;cursor:pointer}.nav ul li *{transition:all 0.5s ease}

.modal-dialog{box-shadow:5px 5px 10px 0px rgba(0,0,0,0.55);padding:2rem;min-width:350px;text-align:center;border-radius:2px;position:absolute;top:40%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}@media (min-width: 768px){.modal-dialog{top:35%;text-align:left}}.modal-dialog::before{content:'';position:absolute;top:0;left:0;right:0;bottom:0;z-index:-5}.form-control{border:none;padding:0.5rem;margin:0.5rem 0;color:white;display:flex;align-items:center}@media (min-width: 768px){.form-control{margin:0.5rem}}.form-label{font-size:20px}.select{border-radius:5px;width:100%;max-width:300px;background-color:#3f3f3f;color:white;cursor:pointer;-webkit-appearance:none;-moz-appearance:none;appearance:none;line-height:1.7;background-image:linear-gradient(45deg, transparent 50%, gray 50%),linear-gradient(135deg, gray 50%, transparent 50%),linear-gradient(to right, #ccc, #ccc);background-position:calc(100% - 20px) calc(1em + 2px),calc(100% - 15px) calc(1em + 2px),calc(100% - 2.5em) 0.5em;background-size:5px 5px, 5px 5px, 1px 1.5em;background-repeat:no-repeat}.select *{color:white}.btn{box-shadow:5px 5px 10px 0px rgba(0,0,0,0.55);border-radius:2px;font-size:24px;border:none;padding:0.25rem 0.75rem;transition:0.1s ease-in-out;cursor:pointer;margin-bottom:1rem}.btn:hover{box-shadow:5px 5px 10px 0px #000}.btn:hover{-webkit-transform:scale(1.02);transform:scale(1.02)}.switch{position:relative;display:inline-block;width:60px;height:34px;margin:0 0.7rem}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#3f3f3f;transition:0.2s;border-radius:5px}.slider:before{position:absolute;content:'';height:26px;width:26px;left:4px;bottom:4px;background-color:white;border-radius:5px;transition:0.25s}input:checked+.slider{background-color:#2196f3}input:checked+.slider:before{-webkit-transform:translateX(26px);transform:translateX(26px)}

.card{border-radius:2px;width:90%;min-width:200px;padding:0.5rem 0}.wide-btn{max-width:300px;padding:0.5rem 0;margin:0 auto}.grid{margin:1.5rem;display:grid;grid-gap:30px 30px;gap:30px 30px;justify-items:center;grid-template-columns:1fr}@media all and (min-width: 768px){.grid{grid-template-columns:repeat(2, 1fr)}}@media all and (min-width: 1024px){.grid{grid-template-columns:repeat(3, 1fr)}}.time-display{position:-webkit-sticky;position:sticky;border-radius:5px;top:10%}

.quickstart-flex{display:flex;justify-content:space-around;align-items:center;flex-direction:column}.quickstart-flex .card{margin:1.5rem;max-width:300px}@media (min-width: 768px){.quickstart-flex{flex-direction:row}.quickstart-flex>p{text-align:left}}

*,body,html{font-family:'Roboto', sans-serif;margin:0;padding:0;box-sizing:border-box}body::before{content:'';position:fixed;top:0;left:0;width:100%;height:100%;z-index:-10}h1{font-size:3rem}a{text-decoration:none}.mt{margin-top:1rem}.mt-big{margin-top:2.5rem}.mb{margin-bottom:1rem}.mb-big{margin-bottom:2.5rem}.container{width:80%;max-width:80%;margin:0 auto;padding:1rem}.text-center{text-align:center}

